'use client';

import { useEffect, useState, useCallback } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { 
  Play, Save, Clock, ArrowLeft, Key, ChevronDown, ChevronUp
} from 'lucide-react';
import { EditorWrapper } from '../../../components/editor/editor-wrapper';
import { AIHelperPanel } from '../../../components/ai/ai-helper-panel';
import { TestCases } from '../../../components/problem/test-cases';
import { RunResults } from '../../../components/problem/run-results';
import { Button } from '../../../components/ui/button';
import { useProblem } from '../../../hooks/use-problem';
import { useEditorState } from '../../../hooks/use-editor-state';
import { useRunner } from '../../../hooks/use-runner';

export default function ProblemPage() {
  const params = useParams();
  const router = useRouter();
  const slug = params.slug as string;
  
  const [showAIPanel, setShowAIPanel] = useState(true);
  const [showTestCases, setShowTestCases] = useState(true);

  const { data: problemData, isLoading, error } = useProblem(slug);
  const problem = problemData?.data;

  // Get problem ID with fallback
  const problemId = problem?.id || slug;
  
  const editor = useEditorState(problemId);
  const { runResult, isRunning, runTests, clearResults } = useRunner();

  const handleRun = useCallback(async () => {
    if (!problem || !editor.code.trim()) return;
    clearResults();
    await runTests(problemId, editor.code, editor.language);
  }, [problem, editor.code, editor.language, problemId, runTests, clearResults]);

  const handleSubmit = useCallback(async () => {
    if (!problem || !editor.code.trim()) return;

    // First run tests
    await handleRun();
    
    if (!runResult?.success) {
      console.warn('Please fix all test cases before submitting.');
      return;
    }

    // TODO: Implement actual submission
    console.log('Submitting solution:', { problemId, code: editor.code, language: editor.language });
    alert('Submission successful! (This is a demo)');
  }, [problem, editor.code, editor.language, problemId, handleRun, runResult]);

  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      // Ctrl/Cmd + Enter to run tests
      if ((e.ctrlKey || e.metaKey) && e.key === 'Enter') {
        e.preventDefault();
        if (problem && editor.code.trim()) {
          runTests(problemId, editor.code, editor.language);
        }
      }

      // Ctrl/Cmd + S to save
      if ((e.ctrlKey || e.metaKey) && e.key === 's') {
        e.preventDefault();
        editor.save();
      }

      // Ctrl/Cmd + Shift + Enter to submit
      if ((e.ctrlKey || e.metaKey) && e.shiftKey && e.key === 'Enter') {
        e.preventDefault();
        handleSubmit();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [problem, editor, problemId, runTests, handleSubmit]);

  // Load saved editor state
  useEffect(() => {
    if (problem && problem.canonicalTestCases) {
      const savedState = localStorage.getItem(`editor-${problemId}`);
      if (savedState) {
        try {
          const parsed = JSON.parse(savedState);
          editor.setCode(parsed.code || '');
          editor.setLanguage(parsed.language || 'javascript');
        } catch (e) {
          console.error('Failed to load saved state:', e);
        }
      }
    }
  }, [problem, problemId, editor]);

  const handleSave = useCallback(() => {
    if (!problem) return;
    editor.save();
  }, [problem, editor]);

  // Loading skeleton
  if (isLoading) {
    return (
      <div className="min-h-screen bg-[#0A0A0A] flex">
        {/* Left skeleton */}
        <div className="w-[30%] bg-[#1C1F2E] border-r border-[#1C1F2E] p-6">
          <div className="space-y-6">
            <div className="h-8 bg-[#0A0A0A] rounded animate-pulse" />
            <div className="h-4 bg-[#0A0A0A] rounded w-3/4 animate-pulse" />
            <div className="space-y-3">
              <div className="h-4 bg-[#0A0A0A] rounded animate-pulse" />
              <div className="h-4 bg-[#0A0A0A] rounded w-5/6 animate-pulse" />
              <div className="h-4 bg-[#0A0A0A] rounded w-4/6 animate-pulse" />
            </div>
          </div>
        </div>
        {/* Middle skeleton */}
        <div className="flex-1 p-6">
          <div className="h-12 bg-[#1C1F2E] rounded animate-pulse mb-4" />
          <div className="flex-1 bg-[#1C1F2E] rounded animate-pulse" />
        </div>
        {/* Right skeleton */}
        <div className="w-[25%] bg-[#1C1F2E] border-l border-[#1C1F2E] p-4">
          <div className="space-y-3">
            <div className="h-6 bg-[#0A0A0A] rounded animate-pulse" />
            <div className="h-4 bg-[#0A0A0A] rounded animate-pulse" />
          </div>
        </div>
      </div>
    );
  }

  // Error state
  if (error || !problem) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-[#0A0A0A]">
        <div className="text-center max-w-md p-8 bg-[#1C1F2E] rounded-2xl border border-[#1C1F2E]">
          <h2 className="text-2xl font-bold text-[#E5E7EB] mb-2">Problem Not Found</h2>
          <p className="text-[#9CA3AF] mb-6">The problem you&apos;re looking for doesn&apos;t exist.</p>
          <Button 
            onClick={() => router.push('/problems')}
            className="bg-[#00FFFF] hover:bg-[#00FFFF]/90 text-[#0A0A0A]"
          >
            <ArrowLeft className="w-4 h-4 mr-2" />
            Browse Problems
          </Button>
        </div>
      </div>
    );
  }

  // Safely check if test cases exist
  const testCases = problem.canonicalTestCases || [];

  return (
    <div className="min-h-screen bg-[#0A0A0A] flex">
      {/* Problem Statement - Left Side */}
      <div className="w-[30%] lg:w-[28%] xl:w-[30%] bg-[#1C1F2E] border-r border-[#1C1F2E] overflow-y-auto">
        <div className="p-6 space-y-6">
          {/* Back Button */}
          <button
            onClick={() => router.back()}
            className="flex items-center gap-2 text-[#9CA3AF] hover:text-[#00FFFF] transition-colors mb-4"
          >
            <ArrowLeft className="w-4 h-4" />
            <span className="text-sm font-medium">Back</span>
          </button>

          {/* Header */}
          <div>
            <h1 className="text-2xl font-bold text-[#E5E7EB] mb-3">{problem.title}</h1>
            
            {/* Meta Info */}
            <div className="flex items-center gap-3 mb-4">
              <span className={`px-3 py-1 rounded-full text-xs font-semibold ${
                problem.difficulty === 'easy' && 'bg-green-500/20 text-green-400 border border-green-500/30',
                problem.difficulty === 'medium' && 'bg-yellow-500/20 text-yellow-400 border border-yellow-500/30',
                problem.difficulty === 'hard' && 'bg-red-500/20 text-red-400 border border-red-500/30'
              }`}>
                {problem.difficulty.charAt(0).toUpperCase() + problem.difficulty.slice(1)}
              </span>
              <span className="text-sm text-[#9CA3AF]">{problem.points} pts</span>
              {problem.timeLimit && (
                <div className="flex items-center gap-1 text-sm text-[#9CA3AF]">
                  <Clock className="w-4 h-4" />
                  {problem.timeLimit}s
                </div>
              )}
            </div>

            {/* Tags */}
            {problem.tags && problem.tags.length > 0 && (
              <div className="flex flex-wrap gap-2 mb-6">
                {problem.tags.map((tag) => (
                  <span
                    key={tag}
                    className="px-2 py-1 bg-[#0A0A0A] text-[#9CA3AF] text-xs rounded border border-[#1C1F2E] hover:border-[#00FFFF]/30 transition-colors"
                  >
                    {tag}
                  </span>
                ))}
              </div>
            )}

            {/* Description */}
            <div className="prose prose-sm max-w-none prose-slate dark:prose-invert mb-6">
              <div 
                className="text-[#E5E7EB] [&>p]:mb-4 [&>h3]:text-xl [&>h3]:font-bold [&>h3]:mb-2 [&>pre]:bg-[#0A0A0A] [&>pre]:border [&>pre]:border-[#1C1F2E] [&>pre]:p-3 [&>pre]:rounded [&>pre]:overflow-x-auto [&>code]:text-[#00FFFF]"
                dangerouslySetInnerHTML={{ __html: problem.description }}
              />
            </div>
          </div>

          {/* Test Cases Toggle */}
          {testCases.length > 0 && (
            <>
              <button
                onClick={() => setShowTestCases(!showTestCases)}
                className="flex items-center justify-between w-full text-left p-3 bg-[#0A0A0A] border border-[#1C1F2E] rounded-lg hover:border-[#00FFFF]/30 transition-colors"
              >
                <span className="text-sm font-semibold text-[#E5E7EB]">Test Cases</span>
                {showTestCases ? <ChevronUp className="w-4 h-4 text-[#9CA3AF]" /> : <ChevronDown className="w-4 h-4 text-[#9CA3AF]" />}
              </button>

              {showTestCases && <TestCases testCases={testCases} />}
            </>
          )}
        </div>
      </div>

      {/* Editor - Middle */}
      <div className="flex-1 flex flex-col bg-[#0A0A0A]">
        <div className="flex-1 flex flex-col p-6">
          {/* Toolbar */}
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-3">
              <Button
                onClick={handleRun}
                disabled={isRunning || !editor.code.trim()}
                className="bg-[#00FFFF] hover:bg-[#00FFFF]/90 text-[#0A0A0A] font-semibold"
              >
                <Play className="w-4 h-4 mr-2" />
                {isRunning ? 'Running...' : 'Run Tests'}
              </Button>
              <Button
                onClick={handleSave}
                className="border border-[#1C1F2E] hover:bg-[#1C1F2E] text-[#E5E7EB] font-semibold"
              >
                <Save className="w-4 h-4 mr-2" />
                Save
              </Button>
              <Button
                onClick={handleSubmit}
                disabled={isRunning || !editor.code.trim()}
                className="bg-[#6C63FF] hover:bg-[#6C63FF]/90 text-white font-semibold"
              >
                Submit Solution
              </Button>
            </div>

            <div className="flex items-center gap-4 text-xs text-[#9CA3AF]">
              <div className="flex items-center gap-1">
                <Key className="w-3 h-3" />
                <span>Ctrl+Enter to run</span>
              </div>
              <div className="flex items-center gap-1">
                <Key className="w-3 h-3" />
                <span>Ctrl+S to save</span>
              </div>
            </div>
          </div>

          {/* Editor */}
          <div className="flex-1 bg-[#1C1F2E] rounded-lg border border-[#1C1F2E] overflow-hidden">
            <EditorWrapper
              initialCode={editor.code}
              language={editor.language}
              onChange={editor.setCode}
              onRun={async (code: string, lang: string) => {
                if (problem) {
                  const result = await runTests(problemId, code, lang);
                  return result || {
                    success: false,
                    output: '',
                    error: 'Run failed',
                    testResults: [],
                  };
                }
                return {
                  success: false,
                  output: '',
                  error: 'Problem not loaded',
                  testResults: [],
                };
              }}
              onSave={editor.save}
              problemId={problemId}
              className="h-full"
            />
          </div>

          {/* Run Results */}
          {runResult && (
            <RunResults 
              result={runResult} 
              isRunning={isRunning}
              className="mt-4"
            />
          )}
        </div>
      </div>

      {/* AI Helper - Right Side */}
      <div className="w-[25%] lg:w-[22%] xl:w-[25%] border-l border-[#1C1F2E]">
        <AIHelperPanel
          problemId={problemId}
          isOpen={showAIPanel}
          onClose={() => setShowAIPanel(false)}
        />
      </div>
    </div>
  );
}
