'use client';

import { CheckCircle, XCircle, Clock, Zap } from 'lucide-react';
import type { RunResult } from '../../types';
import { formatRuntime, formatMemory } from '../../lib/utils';

interface RunResultsProps {
  result: RunResult | null;
  isRunning: boolean;
  className?: string;
}

export function RunResults({ result, isRunning, className }: RunResultsProps) {
  if (isRunning) {
    return (
      <div className={`bg-[#1C1F2E] border border-[#1C1F2E] rounded-lg p-6 ${className}`}>
        <div className="flex items-center gap-3">
          <div className="animate-spin rounded-full h-5 w-5 border-2 border-[#00FFFF] border-t-transparent"></div>
          <span className="text-[#E5E7EB] font-medium">Running tests...</span>
        </div>
      </div>
    );
  }

  if (!result) return null;

  const passedTests = result.testResults.filter(t => t.passed);
  const failedTests = result.testResults.filter(t => !t.passed);

  return (
    <div className={`bg-[#1C1F2E] border ${result.success ? 'border-green-500/30' : 'border-red-500/30'} rounded-lg overflow-hidden ${className}`}>
      {/* Header */}
      <div className={`p-4 ${result.success ? 'bg-green-500/10' : 'bg-red-500/10'}`}>
        <div className="flex items-center gap-3">
          {result.success ? (
            <CheckCircle className="w-6 h-6 text-green-500" />
          ) : (
            <XCircle className="w-6 h-6 text-red-500" />
          )}
          <div>
            <h3 className="text-lg font-bold text-[#E5E7EB]">
              {result.success ? 'All Tests Passed!' : 'Tests Failed'}
            </h3>
            <p className="text-sm text-[#9CA3AF]">
              {passedTests.length} passed, {failedTests.length} failed out of {result.testResults.length} tests
            </p>
          </div>
        </div>
      </div>

      {/* Stats */}
      <div className="p-4 border-t border-[#1C1F2E] flex items-center gap-6 text-sm text-[#9CA3AF]">
        {result.runtime && (
          <div className="flex items-center gap-2">
            <Zap className="w-4 h-4 text-[#00FFFF]" />
            <span className="text-[#00FFFF] font-semibold">{formatRuntime(result.runtime)}</span>
          </div>
        )}
        {result.memory && (
          <div className="flex items-center gap-2">
            <Clock className="w-4 h-4 text-[#6C63FF]" />
            <span className="text-[#6C63FF] font-semibold">{formatMemory(result.memory)}</span>
          </div>
        )}
      </div>

      {/* Test Results */}
      {result.testResults.length > 0 && (
        <div className="p-4 space-y-2 max-h-64 overflow-y-auto">
          {result.testResults.map((test, index) => (
            <div
              key={index}
              className={`p-3 rounded-lg border ${
                test.passed 
                  ? 'bg-green-500/10 border-green-500/30' 
                  : 'bg-red-500/10 border-red-500/30'
              }`}
            >
              <div className="flex items-center justify-between">
                <span className="font-medium text-[#E5E7EB]">Test {index + 1}</span>
                <div className="flex items-center gap-2">
                  {test.passed ? (
                    <CheckCircle className="w-4 h-4 text-green-500" />
                  ) : (
                    <XCircle className="w-4 h-4 text-red-500" />
                  )}
                  <span className={`text-xs font-semibold ${test.passed ? 'text-green-500' : 'text-red-500'}`}>
                    {test.passed ? 'PASSED' : 'FAILED'}
                  </span>
                </div>
              </div>
              {test.errorMessage && (
                <div className="mt-2 text-xs text-red-400 font-mono bg-[#0A0A0A] p-2 rounded">
                  {test.errorMessage}
                </div>
              )}
            </div>
          ))}
        </div>
      )}

      {/* Error Output */}
      {result.error && (
        <div className="p-4 border-t border-[#1C1F2E] bg-red-500/10">
          <div className="text-sm font-medium text-red-500 mb-1">Error:</div>
          <pre className="text-xs text-red-400 font-mono bg-[#0A0A0A] p-3 rounded overflow-x-auto">
            {result.error}
          </pre>
        </div>
      )}
    </div>
  );
}

