'use client';

import { useState } from 'react';
import { ChevronDown, ChevronRight } from 'lucide-react';
import type { TestCase } from '../../types';

interface TestCasesProps {
  testCases: TestCase[];
  className?: string;
}

export function TestCases({ testCases, className }: TestCasesProps) {
  const [isExpanded, setIsExpanded] = useState(true);

  if (!testCases || testCases.length === 0) {
    return null;
  }

  return (
    <div className={className}>
      <div className="flex items-center justify-between mb-4">
        <h3 className="text-lg font-semibold text-[#E5E7EB]">Test Cases</h3>
        <button
          onClick={() => setIsExpanded(!isExpanded)}
          className="text-[#9CA3AF] hover:text-[#E5E7EB] transition-colors"
        >
          {isExpanded ? <ChevronDown className="w-5 h-5" /> : <ChevronRight className="w-5 h-5" />}
        </button>
      </div>

      {isExpanded && (
        <div className="space-y-3">
          {testCases.slice(0, 3).map((testCase, index) => (
            <div key={testCase.id || index} className="bg-[#0A0A0A] border border-[#1C1F2E] rounded-lg p-4">
              <div className="text-sm font-medium text-[#E5E7EB] mb-3 flex items-center gap-2">
                <span className="w-6 h-6 rounded-full bg-[#00FFFF]/20 text-[#00FFFF] flex items-center justify-center text-xs">
                  {index + 1}
                </span>
                {testCase.visibility === 'private' && (
                  <span className="text-xs text-[#9CA3AF]">(Hidden)</span>
                )}
              </div>
              <div className="grid grid-cols-1 gap-3 text-sm">
                <div>
                  <div className="font-medium text-[#9CA3AF] mb-1">Input:</div>
                  <pre className="bg-[#1C1F2E] p-3 rounded text-xs overflow-x-auto text-[#E5E7EB] border border-[#1C1F2E] font-mono">
                    {testCase.input}
                  </pre>
                </div>
                <div>
                  <div className="font-medium text-[#9CA3AF] mb-1">Expected Output:</div>
                  <pre className="bg-[#1C1F2E] p-3 rounded text-xs overflow-x-auto text-[#00FFFF] border border-[#1C1F2E] font-mono">
                    {testCase.expectedOutput}
                  </pre>
                </div>
              </div>
            </div>
          ))}
          {testCases.length > 3 && (
            <div className="text-sm text-[#9CA3AF] text-center py-2">
              +{testCases.length - 3} more test cases
            </div>
          )}
        </div>
      )}
    </div>
  );
}

