import { useState, useEffect, useCallback } from 'react';
import { useAppStore } from '../lib/store';
import type { EditorState } from '../types';

export function useEditorState(problemId: string) {
  const { setEditorState, getEditorState } = useAppStore();
  const [code, setCode] = useState('');
  const [language, setLanguage] = useState('javascript');
  const [isDirty, setIsDirty] = useState(false);

  // Load saved state on mount
  useEffect(() => {
    const savedState = getEditorState(problemId);
    if (savedState) {
      setCode(savedState.code || '');
      setLanguage(savedState.language || 'javascript');
      setIsDirty(savedState.isDirty || false);
    }
  }, [problemId, getEditorState]);

  // Auto-save every 10 seconds
  useEffect(() => {
    if (!isDirty || !code) return;

    const autoSaveTimer = setInterval(() => {
      setEditorState(problemId, {
        code,
        language,
        isDirty: false,
        lastSaved: new Date().toISOString(),
      });
      setIsDirty(false);
    }, 10000); // 10 seconds

    return () => clearInterval(autoSaveTimer);
  }, [problemId, code, language, isDirty, setEditorState]);

  const handleCodeChange = useCallback((newCode: string) => {
    setCode(newCode);
    setIsDirty(true);
    setEditorState(problemId, {
      code: newCode,
      language,
      isDirty: true,
    });
  }, [problemId, language, setEditorState]);

  const handleLanguageChange = useCallback((newLanguage: string) => {
    setLanguage(newLanguage);
    setIsDirty(true);
    setEditorState(problemId, {
      code,
      language: newLanguage,
      isDirty: true,
    });
  }, [problemId, code, setEditorState]);

  const handleSave = useCallback(() => {
    setEditorState(problemId, {
      code,
      language,
      isDirty: false,
      lastSaved: new Date().toISOString(),
    });
    setIsDirty(false);
  }, [problemId, code, language, setEditorState]);

  const getCodeHistory = useCallback(() => {
    const history = localStorage.getItem(`code-history-${problemId}`);
    return history ? JSON.parse(history) : [];
  }, [problemId]);

  const saveToHistory = useCallback((codeSnippet: string) => {
    const history = getCodeHistory();
    const newEntry = {
      code: codeSnippet,
      timestamp: new Date().toISOString(),
    };
    const updatedHistory = [newEntry, ...history].slice(0, 10); // Keep last 10 entries
    localStorage.setItem(`code-history-${problemId}`, JSON.stringify(updatedHistory));
  }, [problemId, getCodeHistory]);

  return {
    code,
    language,
    isDirty,
    setCode: handleCodeChange,
    setLanguage: handleLanguageChange,
    save: handleSave,
    getCodeHistory,
    saveToHistory,
  };
}

